<template>
    <v-row >
  
        <app-table disable-sort hide-default-footer :items="value" :headers="headers" v-if="value.length" style="width: 100%" >
           
        </app-table>
         
        <v-alert v-else type="warning" width="100%"> No data available  </v-alert>
    </v-row>
</template>
<script>
import dateformat from 'dateformat'
import AppTable from './DataTable' 
export default {
    name: 'Assignment',
    components: {
        AppTable
    },
    props: {
        value: Array,
        default: () => []
    },
    data () {
        return {
            dateformat,
            headers: [
                { text: 'Assigned To', value: 'assignedTo.displayName' },
                { text: 'Assigned By', value: 'assignedBy.displayName' },
                { text: 'Date', value: 'createdAt', format: 'date' },
            ]
        }
    } 

}
</script>