
<template>
<v-input v-bind="$attrs" :style="{ width: size + 'px'}" >
  <v-avatar :size="size" >
    
    <v-hover style="position: absolute">
      <template v-slot:default="{ hover }">
        <v-img :src="src" ref="img">
          <v-fade-transition>
            <v-overlay
              v-if="hover"
              absolute
              color="#036358"
            >
              <v-btn icon @click="selectImg" v-if="!value">
                <v-icon>mdi-camera</v-icon>
              </v-btn>
              <v-btn icon @click="removeImg" v-else>
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </v-overlay>
          </v-fade-transition>
        </v-img>
      </template>
    </v-hover>
  </v-avatar>
</v-input>
</template>
<script>
import axios from 'axios'
import  config from '../config'
const { baseURL } = config 
 
 
const getInstance = () => {
    const token = localStorage.getItem('token')
    const instance = axios.create({
        baseURL,
        // timeout: 1000,
        headers: { 'Authorization': 'Bearer ' + token }
    });
    return instance
}

export default {
  name: 'ProfileImage',
  props: {
    size: {
      type: Number,
      default: 75
    },
    value: {
      type: String,
  
    }
  },
  data: () => ({
    overlay: false,
  }),
  computed: {
    src () {
      const origin = `${location.protocol}//${location.hostname}` 
      return this.value ? (origin + ':3000' + this.value) :'/img/blank-profile-picture.png'
    }
  },
  methods:{
    removeImg () {
      const instance = getInstance()
      instance.delete('files', { params: {path: this.value} })
      .then(e => {
        console.log(e)
        this.$emit('input',  null)
      })
      .catch(err => console.log(err))
      
    },
    selectImg () {
      const file = document.createElement('input')
      file.type = 'file'
      file.accept=".jpg,.jpeg,.png"
      file.onchange = this.onChange
      file.click()
    },
    onChange (e) {
      console.log(e);
      const instance = getInstance()
      const file = e.target.files[0];
          const formData = new FormData();        
          formData.append('file', file)
          instance.post('files',formData, {
            onUploadProgress: this.onUploadProgress
        }).then(res => {
            this.$emit('input',  res.data.path)
        }).catch(err => console.log(err))
    },
    onUploadProgress (ProgressEvent) {
      let progress = Math.round(ProgressEvent.loaded / ProgressEvent.total * 100) + '%';
                console.log(progress);
    }
  }
}
</script>